.tb_pro__container{
       border-bottom: 1px solid #0000001a;
       margin-bottom: 20px;
       width: 100%;
}
.tb_pro__wrap {
       display: -webkit-box;
       display: -ms-flexbox;
       display: flex;
       -ms-flex-wrap: wrap;
       flex-wrap: wrap;
       justify-content: center;
       margin: 0 auto;
       width: 100%;
}

.tb_pro__heading{
       font-family: var(--tb-catalogue-font);
       color: var(--tb-catalogue-color);
       font-size: var(--tb-catalogue-size);
       font-style: var(--tb-catalogue-varient);
       font-weight: var(--tb-catalogue-weight);
       margin-bottom: 15px;
       text-align: center;
}

.tb_pro__title {
       font-family: var(--tb-product-font);
       color: var(--tb-product-color);
       font-size: var(--tb-product-size);
       font-weight: var(--tb-product-varient);
       -webkit-line-clamp: 2;
       -webkit-box-orient: vertical;
       display: -webkit-box;
       margin-bottom: 5px;
       overflow: hidden;
       text-overflow: ellipsis;
       width: 100%;
       word-break: break-word;
}

.tb_pro__item {
       -webkit-box-flex: 0;
       -ms-flex: 0 0 50%;
       flex: 0 0 50%;
       max-width: 200px;
       padding-right: 7.5px;
       padding-left: 7.5px;
       display: flex;
}

.tb_pro__price_wrap{
       display: flex;
       align-items: center;
       flex-wrap: wrap;
       justify-content: center;
}

.tb_pro__price {
       display: flex;
       justify-content: center;
       align-items: center;
}

.tb_pro__discount_price {
       text-decoration: line-through;
       transform: scale(.65);
       opacity: .7;
}

.tb_pro__price, .tb_pro__discount_price{
       font-family: var(--tb-price-font);
       color: var(--tb-price-color);
       font-size: var(--tb-price-size);
       font-weight: var(--tb-price-weight);
       font-style: var(--tb-price-varient);
}

.tb_pro__wrap_in {
       text-align: center;
       margin-bottom: 15px;
       padding: 10px;
       border-radius: 5px;
       border: 1px solid rgba(0, 0, 0, 0.07);
       display: flex;
       justify-content: start;
       flex-wrap: wrap;
       flex-direction: column;
       width: 100%;
       align-items: center;
       transition: border .3s ease-in-out;
}

.tb_pro__wrap_in:hover{
       border: 1px solid rgba(0, 0, 0, 0.2);
}

.tb_pro__img_wrapper {
       width: 90px;
       margin-bottom: 15px;
}

.tb_pro__image_wrap {
       position: relative;
}

.tb_pro__image {
       position: absolute;
       width: 100%;
       height: 100%;
       object-fit: contain;
       object-position: center;
       left: 0;
       top: 0;
}

.ugc_pro_wrap {
       position: relative;
       z-index: 2;
       width: 100%;
       height: 100%;
       display: flex;
       flex-wrap: wrap;
       align-content: center;
}
.tb_pro__link{
       width: 100%;
       display: flex;
       align-items: center;
       justify-content: center;
       margin-top: auto;
       padding-top: 10px;
}

.tb_pro__btn {
       font-family: var(--tb-shop-btn-font);
       color: var(--tb-shop-btn-color);
       font-size: var(--tb-shop-btn-size);
       background-color: var(--tb-shop-btn-bg-color);
       //font-weight: var(--tb-shop-btn-varient);
       cursor: pointer !important;
       display: inline-block;
       letter-spacing: 1px;
       max-width: 100%;
       overflow: hidden;
       padding: 8px 20px;
       text-decoration: none !important;
       text-overflow: ellipsis;
       vertical-align: top;
       width: auto;
}
.tb_pro__btn.tb_pro_sold {
       opacity: .5;
       pointer-events: none;
}
.tb_post_modal_product .tb_post_modal_details_right.tb_post_modal_text__ {
       width: calc(393px + 26vh + 26vw);
       max-width: 600px;
}

.tb_post_modal_product .tb_post_modal_text__ .tb_post_modal_post_wrapper{
       min-height: auto;
}

.tb_post_modal_product .tb_pro__wrap{
       max-width: 400px;
}