.tb_post_modal_modal {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 999999999999;
    outline: 0;
    overflow-x: hidden;
    overflow-y: auto;
    background-color: rgba(0, 0, 0, .7);
    visibility: hidden;
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    backface-visibility: hidden;
    opacity: 0;
}

.tb_post_modal_modal.tb_post_wall_modal {
    z-index: 999999997;
}

.tb_post_modal_modal.tb_post_modal_show {
    opacity: 1;
    visibility: visible;
}

.tb_post_modal_modal_dialog {
    display: flex;
    align-items: center;
    max-width: calc(100vw - 130px);
    position: relative;
    width: auto;
    pointer-events: none;
    min-height: calc(100% - (32px * 2));
    margin: 32px auto;
    transform: scale(1.1);
    transition: all 0.2s;
}

.tb_post_modal_modal.tb_post_modal_show .tb_post_modal_modal_dialog {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
    opacity: 1;
}

